.ingreds {
	padding: 49px 22px 40px 38px;
	background-color: rgba(#E1E3F7, .24);
	border-width: 2px;
	border-color: $light-green-2;
	color: #626263;
	@include lg {
		padding: 36px 8px 40px 25px; }
	@include md {
		padding: 18px 8px 25px 18px; }

	&__title {
		margin-bottom: 26px;
		@include lg {
			margin-bottom: 23px; } }

	&__name-field {
		padding-left: 5px;
		padding-right: 15px;
		margin-left: -5px;
		margin-bottom: 27px;
		@include lg {
			margin-bottom: 23px; }
		@include md {
			margin-bottom: 16px; } }

	&__list {
		margin-bottom: 52px;
		@include lg {
			margin-bottom: 45px; }
		@include md {
			margin-bottom: 30px; } }

	&__item {
		display: flex;
		align-items: flex-start;

		&:not(:last-child) {
			margin-bottom: 22px;
			@include md {
				margin-bottom: 15px; } }

		&-text {
			flex: 1 1 auto;
			padding-right: 15px;
			@include md {
				padding-right: 10px; } }

		&-name {
			display: block;
			margin-bottom: 4px;
			word-break: break-word;
			@include fs(18);
			@include lg {
				@include fs(15); }
			@include md {
				margin-bottom: -5px;
				@include fs(10); } }

		&-portion {
			@include fs(15);
			@include lg {
				@include fs(12); }
			@include md {
				@include fs(8); } } }


	&__count {
		display: flex;
		align-items: center;
		flex: 0 0 105px;
		margin-top: -6px;
		color: rgba(#373737, .44);
		@include lg {
			flex: 0 0 94px;
			margin-top: -1px; }
		@include md {
			flex: 0 0 84px; }

		&-display {
			width: 40px;
			background: 0;
			text-align: center;
			@include f('hel700', 20);
			color: inherit;
			@include lg {
				width: 30px;
				@include fs(15); }
			@include md {
				@include fs(11); } }

		&-btn {
			width: 35px;
			height: 35px;
			@include f('hel700', 20);
			line-height: 1;
			color: inherit;
			border-radius: 50%;
			transition: .3s ease-in-out;
			@include lg {
				width: 25px;
				height: 25px;
				@include fs(15);
				line-height: 29px; }
			@include md {
				width: 30px;
				height: 30px;
				line-height: 33px; }

			&:hover {
				box-shadow: inset 0 0 10px currentColor; }

			&_minus {
				background-color: rgba(#9B9B9B, .16); }

			&_plus {
				background-color: rgba(#0CC245, .17); }

			span {
				position: relative;
				top: -3px; } } }

	&__status {
		display: flex;
		align-items: center;
		margin: 0 0 37px -20px;
		padding: 16px 15px;
		border: 1px solid $light-green-2;
		border-radius: 22px;
		@include f('hel700', 22);
		text-align: center;
		@include lg {
			margin: 0 18px 35px 0;
			padding: 11px 13px; }
		@include md {
			margin: 0 12px 23px 0;
			padding: 9px 8px; }

		&-title {
			flex: 1 1 auto;
			padding-right: 20px;
			font-size: inherit;
			text-align: left;
			@include lg {
				@include fs(16);
				padding-right: 10px; }
			@include md {
				padding-right: 0;
				@include fs(12); } }

		&-count {
			flex: 0 0 110px;
			@include lg {
				flex: 0 0 80px;
				@include fs(20); }
			@include md {
				flex: 0 0 70px;
				@include fs(15);
				margin-right: -10px; } } }

	&__actions {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 0 0 -22px;
		font-size: 0;
		@include lg {
			margin: 0 13px 0 -5px; }
		@include md {
			margin: 0 6px 0 -5px; }
		@include sm {
			box-sizing: border-box;
			margin: 0 0 0 -10px; }

		&-btn {
			width: calc(50% - 10px);
			margin: 0 5px;
			@include r(1439) {
				max-width: 152px; }
			@include md {
				max-width: 110px; }
			@include sm {
				max-width: none; } } } }
