.popular {
   padding: 70px 0 17px;
   @include lg {
      padding: 100px 0 10px; }
   @include md {
      padding: 55px 0 10px; }
   @include sm {
      padding: 18px 0 19px; }

   &__title {
      padding-right: 130px;
      @include lg {
         padding-right: 160px; }
      @include md {
         padding-right: 20px; }
      @include sm {
         padding-left: 12px; }
	     margin-bottom: 10px; }

   &__list {
      visibility: hidden;
      position: relative;
      z-index: 1;
      margin-top: -30px;
      padding: 70px 0 40px;
      @include lg {
         padding: 60px 0 0; }
      @include md {
         padding: 50px 0 0; }
      @include sm {
         margin: 0;
         padding: 7px 0 0; }

      &-controls {
         position: absolute;
         top: -23px;
         right: 10px;
         z-index: 1;
         font-size: 0;
         @include lg {
            top: -12px;
            right: 8px; }
         @include md {
            display: none; } }

      &-btn {
         $parent: &;
         display: inline-flex;
         align-items: center;
         justify-content: center;
         width: 50px;
         height: 50px;
         background-color: rgba(#9B9B9B, .1);
         border-radius: 50%;
         cursor: pointer;
         transition: .3s ease-in-out;
         @include lg {
            width: 44px;
            height: 44px; }

         &:not(.is-disabled):hover {
            box-shadow: inset 0 0 15px rgba($black, .2); }

         &_prev {
            margin-right: 3px; }
         &_next {}

         &-icon {
            stroke: $light-green;
            fill: none;
            @include fs(27);
            transition: .5s ease-in-out;
            @include lg {
               @include fs(22); }

            #{$parent}_prev & {
               transform: scaleX(-1); }
            #{$parent}.is-disabled & {
               stroke: #9B9B9B; } } } }

   &__item {
      $parent: &;
      display: block;
      border-radius: 28px;
      overflow: hidden;
      flex-basis: percentage(490 / 1366);
      flex-grow: 0;
      flex-shrink: 0;
      box-shadow: 13px 7px 30px rgba(#7E7C97, .87);
      @include lg {
         flex-basis: percentage(316 / 1206); }
      @include md {
         flex-basis: percentage(196 / 728); }
      @include sm {
         flex-basis: percentage(115 / 300);
         border-radius: 15px; }
      &-img-wrap {
         display: block;
         @include cover-img;
         @include ratio(490, 262);
         @include lg {
            @include ratio(316, 178); }
         @include sm {
            @include ratio(115, 75); }
         &::after {
            opacity: 0;
            @include coverdiv;
            background-color: rgba(#4A4A4A, .47);
            transition: .3s ease-in-out;
            content: '';
            @include sm {
               display: none; }
            #{$parent}:hover & {
               opacity: 1; } }

         img {
            @include coverdiv;
            width: 100%; } }

      &-title {
         opacity: 0;
         position: absolute;
         left: 10%;
         right: 10%;
         bottom: 10%;
         @include fs(25);
         color: $white;
         transition: .3s ease-in-out;
         transform: translateY(30px);
         word-wrap: break-word;
         @include lg {
            bottom: 12%;
            @include fs(18); }
         @include md {
            @include fs(12);
            left: 8%;
            right: 8%; }
         @include sm {
            opacity: 1;
            transform: none;
            left: 0;
            right: 0;
            bottom: 0;
            min-height: 31px;
            padding: 2px 7px 5px;
            @include fs(10);
            background-color: rgba(#0CC245, .6); }

         #{$parent}:hover & {
            transform: translateY(0);
            opacity: 1; } } } }

