.header {
   position: fixed;
   top: 0;
   left: 0;
   z-index: 100;
   width: 100%;
   padding: 20px 0 12px;
   background-color: $white;
   transition: .3s ease-in-out;
   @include lg {
      padding: 10px 0 5px;
      box-shadow: 0 3px 25px 3px rgba($black, .36); }
   @include md {
      padding: 11px 0; }
   @include sm {
      box-shadow: 0 3px 25px 3px rgba($black, .36); }

   &.is-fixed {
      box-shadow: 0 3px 25px 3px rgba($black, .36); }

   &.is-hidden {
      transform: translateY(-100%); }

   &__container {
      visibility: hidden;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 157px 0 57px;
      @include lg {
         padding: 0 75px 0 50px; }
      @include md {
         padding: 0 20px; }
      @include sm {
         padding: 0 14px; } }

   &__bar-logo {
      position: relative;
      @include flexCenter;
      width: 150px;
      height: 150px;
      margin-top: -8px;
      padding: 25px;
      text-align: center;
      @include f('din400', 22);
      line-height: 1;
      letter-spacing: 0.025em;
      font-weight: 700;
      background-color: $light-green;
      color: $white;
      transition: .3s ease-in-out;
      @include lg {
         top: 2px;
         width: 154px;
         height: 42px;
         padding: 0 15px;
         @include fs(18);
         font-weight: normal; }
      @include md {
         top: 4px;
         width: 98px;
         height: 26px;
         padding: 0 10px;
         @include fs(12); }
      @include sm {
         width: 28px;
         height: 28px;
         @include fs(8);
         border-radius: 5px;
         line-height: 0.9;
         letter-spacing: 0; }

      &:hover {
         background-color: #01805b; }

      &::before {
         @include vcenter;
         z-index: 1;
         width: 76%;
         height: 76%;
         border: 3px solid $white;
         content: '';
         @include lg {
            border-width: 2px;
            width: 90%;
            height: 74%; }
         @include md {
            border-width: 1px; }
         @include sm {
            display: none; } }

      &-text {
         padding-top: 5px;
         @include md {
            padding-top: 2px; } } }

   &__main-logo {
      margin: 55px 0 40px 176px;
      @include hide-text;
      @include lg {
         transform: scale(.67);
         margin: -10px 0 0 24px; }
      @include md {
         position: absolute;
         top: -2px;
         left: 47.4%;
         z-index: 1;
         transform: translateX(-50%) scale(.42); }
      @include sm {
         left: 40.4%;
         transform: translateX(-50%) scale(.38); } }

   &__link {
      $parent: &;
      position: relative;
      margin-top: 25px;
      color: $red;
      @include f('hel700', 30);
      border-bottom: em(2, 30) solid currentColor;
      line-height: 1;
      transition: .3s ease-in-out;
      @include lg {
         margin-top: -8px;
         @include fs(20); }
      @include md {
         margin-top: 0;
         @include fs(15); }
      @include sm {
         display: none; }

      &:hover {
         border-bottom-color: transparent; }

      &-icon {
         position: absolute;
         top: -0.85em;
         right: -0.25em;
         font-size: 18px;
         transition: .3s ease-in-out;
         @include md {
            top: -0.6em;
            right: -0.55em;
            transform: scale(.6); } } } }
