.info {
   border-color: $orange;
   background-color: rgba(#BDE6AB, .03);
   @include md {
      margin-top: -10px;
      border-width: 2px; }

   &__title {}
   &__head {
      &-arrow {
         stroke: $orange; } }
   &__list {
      @include fs(14);
      line-height: 1.3em;
      @include lg {
         @include fs(13); }
      @include md {
         @include fs(10); }

      &:not(:last-child) {
         margin-bottom: 20px;
         @include lg {
            margin-bottom: 15px; } }

      &-term {
         @include f('av700');
         color: #4A4A4A; }
      &-def {
         @include f('av400'); } }
   &__content {
      &-inner {
         padding: 6px 38px 56px;
         @include lg {
            padding: 10px 26px 36px; }
         @include md {
            padding: 10px 26px 21px; } } } }
