.summary {
   color: rgba(#373737, .7);
   border-color: $light-green-2;
   &__head {
      &-arrow {
         stroke: $light-green; }

      &-title {
         text-align: left; } }
   &__content {
      &-inner {
         padding: 0 22px 25px;
         @include lg {
            padding: 20px 26px 22px; }
         @include md {
            padding: 10px 20px 15px; } } }

   &__stat {
      padding: 20px 15px;
      margin-bottom: 20px;
      border-radius: 22px;
      background-color: rgba(#C9E2D4, .45);
      @include lg {
         padding: 12px 8px; }
      @include md {
         margin-bottom: 4px;
         border-radius: 10px; }

      &-row {
         display: flex;
         justify-content: space-between;
         padding: 0 10px;
         @include f('hel700', 20);
         line-height: 1;
         @include lg {
            @include fs(18); }
         @include md {
            @include fs(14); }

         &:nth-child(1) {
            color: #4A4A4A; }
         &:nth-child(2) {
            color: $light-green; }

         &:not(:first-child) {
            margin-top: 30px;
            @include lg {
               margin-top: 20px; }

            @include md {
               margin-top: 10px; } } }

      &-term {
         padding-right: 15px; }

      &-def {
         flex: 0 0 100px;
         text-align: center;
         @include lg {
            flex: 0 0 80px; }
         @include md {
            margin-right: -20px; } } }

   &__table {
      margin-bottom: 22px;
      @include f('hel700', 18);
      @include lg {
         @include fs(15); }
      @include md {
         margin-bottom: 8px;
         @include fs(12); }

      &-wrap {
         padding: 0 12px;
         @include lg {
            padding: 0; } }

      &-row:first-child &-data:nth-child(2),
      &-row:first-child &-data:nth-child(3) {
         width: 85px;
         text-align: center;
         @include lg {
            width: 65px; }
         @include md {
            width: 50px; } }

      &-row:first-child ~ &-row {
         color: rgba(#373737, .44);
         text-align: center; }

      &-row:first-child ~ &-row &-data:first-child {
         padding: 2px 5px 2px 62px;
         text-align: left;
         @include f('hel400');
         @include lg {
            padding: 2px 5px 2px 42px; } }
      &-row:first-child ~ &-row:last-child &-data {
         padding: 2px 5px 14px 62px;
         @include lg {
            padding: 2px 5px 14px 42px; } }

      &-row:first-child ~ &-row:last-child &-data:nth-child(2),
      &-row:first-child ~ &-row:last-child &-data:nth-child(3) {
         padding: 2px 10px; }

      &-row:first-child ~ &-row &-data {
         padding: 2px 5px; }

      &-body {
         &:not(:first-child) {
            border-top: 1px solid #979797; } }

      &-row {}

      &-data {
         padding: 10px 5px;
         @include lg {
            padding: 6px 5px; }
         @include md {
            padding: 6px 2px; }

         &-symbol {
            text-align: center;
            @include fs(12);
            color: $orange;
            @include lg {
               display: none; } } } }

   &__info {
      margin-bottom: 22px;
      padding: 16px 0;
      font-size: 0;
      border: 1px solid $light-green-2;
      border-radius: 22px;
      @include md {
         margin-bottom: 7px;
         padding: 10px 0;
         border-radius: 10px; }
      @include sm {
         padding: 10px 0 10px 4px; }

      &-item {
         display: inline-flex;
         width: 50%;
         padding: 0 18px;
         @include f('hel700', 18);
         @include lg {
            padding: 0 15px;
            @include fs(15); }
         @include md {
            padding: 0 8px;
            @include fs(12); }

         &:nth-child(2) ~ & {
            margin-top: 15px;
            @include lg {
               margin-top: 12px; }
            @include md {
               margin-top: 6px; } } }

      &-term {
         width: 100%;
         padding-right: 15px;
         @include md {
            padding-right: 5px; } }
      &-def {
         width: 50px;
         @include md {
            width: 40px; } } }

   &__hint {
      margin-bottom: 30px;
      padding-left: 10px;
      @include lg {
         margin-right: -8px;
         padding-left: 0; }
      @include md {
         margin-bottom: 25px; }
      &-text {
         @include fs(14);
         font-style: italic;
         @include lg {
            @include fs(12); }
         @include md {
            @include fs(10); }

         &:not(:last-child) {
            margin-bottom: 15px;
            @include md {
               margin-bottom: 0; } }
         > span {
            display: inline-block;
            padding-left: 38px;
            @include md {
               padding-left: 0; } } } }
   &__warn {
      margin-bottom: 60px;
      padding: 42px 20px;
      background-color: #FDD3D3;
      border-radius: 22px;
      @include lg {
         margin-bottom: 20px;
         padding: 18px 0; }
      @include md {
         margin-bottom: 15px; }

      &-head {
         display: flex;
         align-items: center;
         margin-bottom: 26px;
         @include lg {
            justify-content: center; }
         @include md {
            margin-bottom: 15px; } }
      &-icon {
         margin-right: 12px; }
      &-title {
         color: $red;
         @include fs(20);
         @include lg {
            @include fs(15); }
         @include md {
            @include fs(12); } }

      &-list {
         column-count: 2;
         column-gap: 0;
         @include f('hel700', 18);
         color: rgba(#373737, .7);
         text-align: left;
         @include lg {
            @include fs(15); }
         @include md {
            @include fs(12); }
         li {
            padding-left: 32px;
            margin-bottom: 20px;
            @include md {
               padding-left: 20px;
               margin-bottom: 10px; } } } }
   &__bottom {
      text-align: center;
      &-link {} } }

