.category {
	$parent: &;
	position: relative;
	box-shadow: 13px 7px 30px rgba(0, 0, 0, 0.21);
	border-radius: 22px;
	visibility: hidden;
	@include md {
		border-radius: 10px; }

	&_default {
		&:not(:first-child) {
			margin-top: 36px;
			@include lg {
				margin-top: 28px; } } }

	&__head {
		padding: 22px 100px 27px 48px;
		@include lg {
			padding: 17px 93px 19px 37px; }
		@include md {
			padding: 8px 55px 8px 26px; }
		@include sm {
			padding: 12px 20px 12px 16px; }

		&:hover {
			&::after {
				opacity: 1; } }

		&::after {
			position: absolute;
			top: -1px;
			left: -1px;
			right: -1px;
			bottom: -1px;
			opacity: 0;
			transition: .35s .15s ease-in-out;
			border: 2px solid $light-green-2;
			border-radius: 22px;
			pointer-events: none;
			content: '';
			@include md {
				border-radius: 10px; } }

		&.is-open {
			&::after {
				opacity: 1; } }

		&-arrow {
			margin-top: 0;
			margin-right: 38px;
			stroke: $light-green;
			@include lg {
				margin-top: 10px;
				margin-right: 32px; }
			@include md {
				margin-top: 2px;
				margin-right: 13px; }
			@include sm {
				margin-right: 8px;
				@include fs(10); } }

		&-icon {
			position: absolute;
			@include lg {
				transform: scale(.85); }
			@include md {
				transform: scale(.5); }
			@include sm {
				transform: scale(.35); }

			&.icon-salad-1 {
				right: 32px;
				@include lg {
					right: 25px;
					top: 26px; }
				@include md {
					top: 8px;
					right: 7px; }
				@include sm {
					top: 10px;
					right: -4px; } }

			&.icon-cabbage {
				top: 27px;
				right: 41px;
				@include lg {
					top: 21px;
					right: 38px; }
				@include md {
					top: 6px;
					right: 15px; }
				@include sm {
					top: 6px;
					right: 9px; } }

			&.icon-cucumber {
				right: 46px;
				top: 29px;
				@include lg {
					top: 15px; }
				@include md {
					top: 6px;
					right: 18px; }
				@include sm {
					top: 4px;
					right: 8px; } }

			&.icon-cherry {
				top: 22px;
				right: 14px;
				@include lg {
					top: 22px; }
				@include md {
					top: 0;
					right: 0; }
				@include sm {
					top: 4px;
					right: -6px; } }

			&.icon-chicken {
				top: 21px;
				right: 21px;
				@include lg {
					top: 18px;
					right: 29px; }
				@include md {
					top: 2px;
					right: 6px; }
				@include sm {
					right: 1px; } }

			&.icon-potato {
				top: 17px;
				right: 23px;
				@include lg {
					top: 12px; }
				@include md {
					top: -2px;
					right: 4px; }
				@include sm {
					right: -4px; } }

			&.icon-leaf {
				right: 20px;
				top: 20px;
				@include lg {
					right: 30px;
					top: 16px; }
				@include md {
					top: 1px;
					right: 8px;
					transform: scale(.45); }
				@include sm {
					right: 0; } } } }

	&__title {
		text-align: left;
		color: $light-green;
		@include sm {
			@include fs(15); } }


	&__content {
		&-inner {
			display: flex;
			flex-wrap: wrap;
			padding: 5px 6px 56px 20px;
			@include lg {
				padding: 5px 6px 25px 20px; }
			@include md {
				padding: 5px 6px 25px 15px; }
			@include sm {
				padding: 5px 10px 25px 10px; } } }

	&__comp {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		width: calc(25% - 15px);
		min-height: 262px;
		padding: 12px 20px 23px;
		border-radius: 22px;
		background-color: $white;
		cursor: pointer;
		-webkit-tap-highlight-color: transparent;
		transition: .3s ease-in-out;
		@include fs(16);
		@include lg {
			min-height: 232px;
			padding: 5px 20px 15px;
			@include fs(13); }
		@include md {
			width: calc(33.33333% - 10px);
			min-height: 174px;
			padding: 12px 14px; }
		@include sm {
			width: calc(50% - 5px);
			min-height: 155px;
			border-radius: 10px; }

		#{$parent}_default & {
			margin-top: 15px;
			@include md {
				margin-top: 10px; }

			&:not(:nth-child(4n)) {
				margin-right: 15px;
				@include md {
					margin-right: 10px; } }

			&:not(:nth-child(3n)) {
				@include md {
					margin-right: 10px; } }

			&:nth-child(2n) {
				@include sm {
					margin-right: 0 !important; } } }

		&:hover {
			box-shadow: 13px 7px 30px rgba(0, 0, 0, 0.21); }

		&-img-wrap {
			display: block;
			margin-bottom: 8px;

			img {
				display: block;
				width: 100%;
				height: 100%;
				object-fit: contain;
				font-family: 'object-fit: contain;'; } }

		&-title {
			padding-bottom: 10px;
			color: #717171;
			font-size: inherit;
			word-break: break-word;
			@include sm {
				padding-bottom: 5px;
				@include fs(10); } }

		&-portion {
			position: relative;
			display: flex;
			align-items: center;
			margin-top: auto;
			padding-left: 22px;
			color: #9b9b9b;
			@include lg {
				@include fs(12); }
			@include md {
				@include fs(8); }

			&-icon {
				position: absolute;
				top: 2px;
				left: 0;
				@include md {
					top: -1px;
					transform: scale(.65); } }

			span {
				margin-right: 4px; } }

		&-visual {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 1;
			border: 3px solid transparent;
			border-radius: 22px;
			transition: .3s ease-in-out;
			pointer-events: none;
			@include sm {
				border-radius: 10px; } }

		&-control {
			display: none;
			&:checked ~ #{$parent}__comp-visual {
				border-color: $light-green;
				box-shadow: 13px 7px 30px rgba(0, 0, 0, 0.21); } } } }

.print-ingreds {
	display: none; }
